import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  EventEmitter,
  Input,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { MatTab, MatTabChangeEvent, MatTabGroup, MatTabsModule } from '@angular/material/tabs';
import { coerceNumberProperty, NumberInput } from '@angular/cdk/coercion';

import { DscTabComponent } from './dsc-tab/dsc-tab.component';
import { CommonModule, NgClass, NgComponentOutlet, NgFor, NgIf, NgTemplateOutlet } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { DscBadgeDirective } from 'sidsc-components/dsc-badge';

export type DscTabGroupSize = 'large' | 'standard';
export type DscIconStyle = 'outlined' | 'filled' | 'rounded' | 'sharp' | 'two-tone';

export interface DscTabModel {
  label: string;
  icon?: string;
  iconStyle?: DscIconStyle;
  disabled?: boolean;
  content: TemplateRef<any>;
}


@Component({
  selector: 'dsc-tab-group',
  standalone: true,
  imports: [MatTabsModule, NgClass, NgFor, NgIf, NgTemplateOutlet, NgComponentOutlet, CommonModule, MatIconModule, DscBadgeDirective],
  encapsulation: ViewEncapsulation.None,
  templateUrl: './dsc-tab-group.component.html',
  styleUrls: ['./dsc-tab-group.component.scss']
})
export class DscTabGroupComponent implements AfterViewInit {
  // I use the @ViewChild decorator to grab MatTabGroup from the host view.
  @ViewChild(MatTabGroup)
  matTabGroup!: MatTabGroup;

  // I use the @ContentChildren decorator to grab DscTabComponent within the ng-content tag.
  @ContentChildren(DscTabComponent)
  tabs!: QueryList<DscTabComponent>;

  @Input()
  animationDuration: string = "600ms";

  @Input() tabSize: 'large' | 'standard' = 'standard';

  @Input() 
  set dynamicTabs(value: DscTabModel[]) {
    this._dynamicTabs = value ?? [];
    queueMicrotask(() => this.remergeProjectedTabs());
  }
  get dynamicTabs(){
    return this._dynamicTabs;
  }

  @Input()
  get selectedIndex(): number {
    return this._selectedIndex;
  }

  set selectedIndex(value: NumberInput) {
    this._selectedIndex = coerceNumberProperty(value);
    if (this.matTabGroup) {
      this.matTabGroup.selectedIndex = this._selectedIndex;
    }
  }

  private _selectedIndex = 0;

  private _dynamicTabs: DscTabModel[] = [];

  @Output()
  selectedIndexChange: EventEmitter<number> = new EventEmitter;

  @Output()
  selectedTabChange: EventEmitter<MatTabChangeEvent> = new EventEmitter;

  @Output()
  focusChange: EventEmitter<MatTabChangeEvent> = new EventEmitter;

  constructor(private cdr: ChangeDetectorRef) {}

  // The QueryList is initialized only before the ngAfterViewInit lifecycle hook, therefore, is available only from this point.
  // ngAfterViewInit(): void {
  //   const matTabsFromQueryList: MatTab[] = this.tabs.map((tab: DscTabComponent) => tab.matTab);
  //   this.matTabGroup._tabs.reset([matTabsFromQueryList]);
  //   this.matTabGroup._tabs.notifyOnChanges();
  // }

  ngAfterViewInit(): void {
    this.remergeProjectedTabs();
    this.tabs.changes?.subscribe(() => this.remergeProjectedTabs());
    (this.matTabGroup as any)._tabs?.changes?.subscribe(() => this.remergeProjectedTabs());
  }

  ngAfterContentInit(): void {
    setTimeout(() => {
      if (this.matTabGroup) {
        this.matTabGroup.selectedIndex = this._selectedIndex;
      }
    });
  }

  private remergeProjectedTabs(): void{
    if (!this.matTabGroup) return;
    const ql = (this.matTabGroup as any)._tabs as import('@angular/core').QueryList<MatTab>;
    if (!ql) return;
    const current = ql.toArray();
    const projected = (this.tabs ?? []).map(t => t.matTab).filter(Boolean) as MatTab[];
    const missing = projected.filter(p => !current.includes(p));
    if (missing.length === 0) return;

    ql.reset([...current, ...missing]);
    ql.notifyOnChanges();
    this.cdr.detectChanges();
  }


  iconClass(style: DscIconStyle): string {
    if (style === 'filled') return 'material-icons';
    if (style === 'rounded') return 'material-icons-round';
    return `material-icons-${style}`;
  }

  isImage(icon: any): boolean {
    if (typeof icon !== 'string') return false;
    const lower = icon.toLowerCase();
    return lower.endsWith('.svg') || lower.endsWith('.png');
  }

  onSelectedIndexChange(event: number) {
    this.selectedIndexChange.emit(event);
  }

  onSelectedTabChange(event: MatTabChangeEvent) {
    this.selectedTabChange.emit(event);
  }

  onFocusChange(event: MatTabChangeEvent) {
    this.focusChange.emit(event);
  }
}
