import { Component, Input, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatTab, MatTabsModule } from '@angular/material/tabs';
import { MatIconModule } from '@angular/material/icon';
import { NgIf, NgClass, NgFor, NgTemplateOutlet, NgComponentOutlet, CommonModule } from '@angular/common';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { DscBadgeDirective } from 'sidsc-components/dsc-badge';



@Component({
  selector: 'dsc-tab',
  standalone: true,
  imports: [MatTabsModule, NgClass, NgFor, NgIf, NgTemplateOutlet, NgComponentOutlet, CommonModule, MatIconModule, DscBadgeDirective],
  encapsulation: ViewEncapsulation.None,
  templateUrl: './dsc-tab.component.html',
  styleUrls: ['./dsc-tab.component.scss']
})
export class DscTabComponent {
  @ViewChild(MatTab)
  matTab!: MatTab;

  @Input() label: string = '';

  @Input() icon?: string;

  @Input() iconStyle?: 'outlined' | 'filled' | 'rounded' | 'sharp' | 'two-tone' = 'filled';

  @Input() dscBadge?: string;

  @Input() dscBadgeSize: 'small' | 'standard' | 'large' = 'standard';

  @Input()
  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: BooleanInput) {
    this._disabled = coerceBooleanProperty(value);
  }

  private _disabled: boolean = false;

  getIconClass(): string {
    if (this.iconStyle === 'filled') return 'material-icons';
    if (this.iconStyle === 'rounded') return 'material-icons-round';
    return `material-icons-${this.iconStyle}`;
  }

  isImage(icon: any): boolean {
    if (typeof icon !== 'string') return false;
    const lower = icon.toLowerCase();
    return lower.endsWith('.svg') || lower.endsWith('.png');
  }

}
